  cursor: pointer;
  /*border-radius: 3px;*/
  /*box-shadow: -1px 1px 6px -1px rgba(0,0,0,.5);*/
  -webkit-box-shadow: -1px 1px 0px 2px rgba(0,0,0,.5);
          box-shadow: -1px 1px 0px 2px rgba(0,0,0,.5);  
  font-family: Ubuntu, sans-serif;
  color: #000;
  font-size: 1rem;
}

.projects {
  /*width: 50%;*/
  /*height: 100%;*/
